.demo {
    &__container {
        background-color: $white;
        text-align: center;
    }

    &__actions {
        margin: 35px 20px 66px;
    }

    &__start-btn {
        @include btn($size-m);
        @include btn-primary;
        margin: 0 20px 24px 0;
    }

    &__try-btn {
        @include btn(172px);
        @include btn-secondary($btn-secondary-color, $btn-secondary-bd-color);
    }

    &__title {
        @include make-h3;
        font-family: $Sintony;
        font-weight: normal;
        color: $text-primary;
        margin: 0 0 12px;
    }

    &__text {
        max-width: 940px;
        @include P20;
        font-family: $Nexa-Regular;
        line-height: 1.65;
        color: $text-secondary;
        margin: 0 20px 103px;
    }
}

@media screen and (max-width: 425px) {
    .demo {
        &__actions {
            margin-bottom: 40px;
        }

        &__text {
            margin-bottom: 40px;
        }
    }
}