.footer {
    &__container {
        background-color: $white;
    }

    &__row {
        display: flex;
        justify-content: space-evenly;
    }

    &__title {
        @include make-h5;
        color: $text-secondary;
        text-transform: uppercase;
        padding: 58px 0 20px;
        margin: 0;
    }

    &__menu {
        display: flex;
        flex-direction: column;
        padding: 0;
        margin: 0;
    }

    &__link {
        text-decoration: none;
        font-size: 14px;
        line-height: 25px;
        font-weight: 600;
        color: $text-secondary;

        &:not(:last-child) {
            margin-bottom: 7px;
        }
    }

    &__icon {
        background-image: url('../../public/img/pencil.png');
        background-repeat: no-repeat;
        width: 15px;
        height: 44px;
        margin: 12px auto;
    }
}

@media screen and (max-width: 768px) {
    .footer {
        &__col {
            margin: 0 12px;
        }
    }
}

@media screen and (max-width: 425px) {
    .footer {
        &__row {
            flex-direction: column-reverse;
        }

        &__col {
            text-align: center;
        }

        &__title {
            padding-top: 32px;
        }
    }
}